<script>
  let recordData = [];
  let loading = true;
  import { get } from "svelte/store";
  import { onMount } from "svelte";

  async function fetchGameData() {
    let checkjwt = localStorage.getItem("d2rpvpjwtToken")
    if (checkjwt) {
      try {
        // fetch GET  .
        const response = await fetch("/no_approved_record", {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
          },
        });

        //  JSON  .
        recordData = await response.json();
        loading = false;
      } catch (error) {
        alert("    ");
      }
    } else {
      alert(" ");
    }
  }

  onMount(async () => {
    fetchGameData();

  });

  async function approveRecord(orderNum) {
    //    .
    const data = { orderNum: orderNum };
    let checkjwt = localStorage.getItem("d2rpvpjwtToken")
    if (checkjwt) {
      try {
        const response = await fetch("/approve-record", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
          },
          body: JSON.stringify(data), // Send the JSON data as the body of the request
        });
        if (response.ok) {
          alert(" ");
          fetchGameData();
        }

        else if (response.status===403) {
          await refresh_access_token()
           checkjwt = localStorage.getItem("d2rpvpjwtToken")
      
           const response = await fetch("/approve-record", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
          },
          body: JSON.stringify(data), // Send the JSON data as the body of the request
        });
        if (response.ok) {
          alert(" ");
          fetchGameData();
        }

          }


      } catch (error) {
        alert(" ", error);
      }
    } else {
      alert(" ");
    }
  }

  async function deleteRecord(orderNum) {
    const data = { orderNum: orderNum };
    let checkjwt = localStorage.getItem("d2rpvpjwtToken")
    if (checkjwt) {
      try {
        const response = await fetch("/delete-record", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
          },
          body: JSON.stringify(data), // Send the JSON data as the body of the request
        });
        if (response.status===200) {
          alert(" ");
          fetchGameData();
        } else if (response.status===403) {
          await refresh_access_token()
           checkjwt = localStorage.getItem("d2rpvpjwtToken")
           const response = await fetch("/delete-record", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
          },
          body: JSON.stringify(data), // Send the JSON data as the body of the request
        });
        if (response.status===200) {
          alert(" ");
          fetchGameData();
        }
      }


      } catch (error) {
        alert(" ", error);
      }
    } else {
      alert(" ");
    }
  }
</script>

<table>
  <tr>
    <th class="record-table-date">  </th>
    <th class="record-table-loser">  </th>
    <th class="record-table-score">  </th>
    <th class="record-table-ok">  </th>
  </tr>
  {#if loading}<tr> <td colspan="4" class="text-center"> ..</td></tr>{/if}
  {#if !loading && recordData.length == 0}<tr>
      <td colspan="4" class="text-center"> </td></tr
    >{/if}

  {#each recordData as row}
    <tr>
      <td
        >{new Date(row.Date).toLocaleString("en-US", {
          month: "2-digit",
          day: "2-digit",
        })}</td
      >
      <!--    JSON . -->
      <td>{row.Loser}</td>
      <td>{row.LScore}</td>
      <td>
        <button on:click={() => approveRecord(row.OrderNum)}></button>
        <button on:click={() => deleteRecord(row.OrderNum)}></button>
      </td>
    </tr>
  {/each}
</table>

<style>
  table {
    width: 90%;
    margin-top: 20px;
  }
  .record-table-date {
    width: 15%;
  }
  .record-table-loser {
    width: 40%;
  }
  .record-table-score {
    width: 15%;
  }
  .record-table-ok {
    width: 20%;
  }
</style>
