<script>
  import { onMount } from "svelte";

  import {
    nickname,
    nicknames,
    fetchNicknames, refresh_access_token,
    mode,
  } from "../store.js";
  let player;
  let playerscore;
  function handle_player_change(event) {
    player = event.target.value;
  }
  function handle_playerscore_change(event) {
    playerscore = event.target.value;
  }

  onMount(async () => {
    fetchNicknames($mode); //  
  });

  async function submit_bonus_score() {

      const data = { player: player, playerScore: playerscore };
      let checkjwt = localStorage.getItem("d2rpvpjwtToken")

      try {
        const response = await fetch("/submit-admin-score", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
          },
          body: JSON.stringify(data), // Send the JSON data as the body of the request
        });
        if (response.status === 200) {
          alert("  ");
        } 
        else if (response.status === 403) {
            await refresh_access_token()
            checkjwt = localStorage.getItem("d2rpvpjwtToken")
            const response = await fetch("/submit-admin-score", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${checkjwt}`,
            },
            body: JSON.stringify(data), // Send the JSON data as the body of the request
          });
          if (response.status===200) {
            alert("  ");
          }


          }
          else {
            alert(" ");
          }

      } catch (error) {
        alert("  :", error);
      }
  }

  async function score_reset() {
    let userResponse = confirm(
      "   . ?"
    );

    if (userResponse) {
        try {
          const response = await fetch("/reset-rank", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${checkjwt}`,
            },
          });
          if (response.status===200) {
            alert(" ");
          }
          else if (response.status===403) {
           await refresh_access_token()
           checkjwt = localStorage.getItem("d2rpvpjwtToken")
            const response = await fetch("/reset-rank", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${checkjwt}`,
            },
          });
          if (response.status===200) {
            alert(" ");
          }


          }
          else {
            alert(" ");
          }
        } catch (error) {
          alert("  :", error);
        }
    }
  }
</script>

<div class="main_data">
  {#if $nickname === "admin"}
    <div class="left">
         
      <select
        bind:value={player}
        on:change={handle_player_change}
        class="namewidth"
      >
        {#each $nicknames as option}
          <option value={option}>{option}</option>
        {/each}
      </select>
      <input
        type="number"
        bind:value={playerscore}
        on:change={handle_playerscore_change}
        placeholder=""
        class="scorewidth"
      />
      <button on:click={() => submit_bonus_score()}> </button>
      <br />
      <button on:click={() => score_reset()}>  </button>
    </div>
  {:else}
    <div class="left"> .</div>
  {/if}
</div>
